import { Component } from '@angular/core';

@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.scss']
})
export class AppComponent {
	public text: String = '';

	private timer: any;
	private values: String[];
	private valueIndex: number;
	private button: HTMLButtonElement;

	constructor() {}

	public onClick(e: MouseEvent) {
		// check if new button was clicked
		if (this.button !==  <HTMLButtonElement>e.currentTarget) {
			this.button = <HTMLButtonElement>e.currentTarget;

			this.values = (<HTMLSpanElement>this.button.children[0]).innerText.split(' ');
			this.valueIndex = 0;

			clearTimeout(this.timer);
			this.timer = undefined;
		}

		if (this.timer) {
			clearTimeout(this.timer);
			this.text = this.text.slice(0, this.text.length - 1) + <string>this.values[this.valueIndex++];
		} else {
			this.valueIndex = 0;
			this.text = this.text + <string>this.values[this.valueIndex++];
		}

		this.timer = setTimeout( () => {
			this.timer = undefined;
		}, 800);

		this.valueIndex = this.valueIndex === this.values.length ? 0 : this.valueIndex;
	}
}



